import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { VendorService } from './vendor.service';
import { Vendor } from './vendor';
import { MatTableDataSource, MatDialog, MatPaginator, MatSort } from '../../../../../../node_modules/@angular/material';
import { Router } from '../../../../../../node_modules/@angular/router';
import { EditVendorComponent } from './edit-vendor/edit-vendor.component';
import { DeleteVendorComponent } from './delete-vendor/delete-vendor.component';

@Component({
  selector: 'app-vendor',
  templateUrl: './vendor.component.html',
  styleUrls: ['./vendor.component.css']
})
export class VendorComponent implements OnInit {
  private vendor: Vendor[];
  dataSource;
  private organizationName;

  constructor(private vendorService: VendorService,
    private _dialog: MatDialog,
    private router: Router) { }
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild('filter') filter: ElementRef;
  @ViewChild(MatSort) sort: MatSort;
  displayedColumns = ['vendorId', 'vendorName', 'phoneNo', 'emailId', 'actions'];
  ngOnInit() {
    this.getVendorList();
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  getVendorList() {
    this.vendorService.getVendorList().subscribe((vendor) => {
      this.vendor = vendor;
      this.dataSource = new MatTableDataSource(vendor);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }, err => {
      alert('error');
    })
  }
  addVendor() {
    this.router.navigateByUrl('/restaurants/admin/vendor/addVendor');
  }
  editVendor(i: number, vendorId: number, vendorName: string, organizationName: string, phoneNo: string, emailId: string, vendorCategory: string, address: string, city: string, district: string, state: string, country: string) {
    const dialogRef = this._dialog.open(EditVendorComponent, {
      data: { vendorId: vendorId, vendorName: vendorName, organizationName: organizationName, phoneNo: phoneNo, emailId: emailId, vendorCategory: vendorCategory, city: city, district: district, state: state, country: country }
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result === 1) {
        this.getVendorList();
      }
    });
  }
  deleteVendor(i: number, vendorId: number, vendorName: string, organizationName: string, phoneNo: string, emailId: string) {
    const dialogRef = this._dialog.open(DeleteVendorComponent, {
      data: { vendorId: vendorId, vendorName: vendorName, organizationName: organizationName, phoneNo: phoneNo, emailId: emailId }
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result === 1) {
        this.getVendorList();
      }
    });
  }
}
